uToolsUtils.playPromptTone = function (fileName) {
  var pre = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : false;
  var audio = new Audio();
  audio.loop = false;

  if (pre) {
    audio.play();
    var fig = true;
    audio.addEventListener('ended', function () {
      if (fig) {
        audio.src = "https://xiaou520.gitee.io/sound/".concat(fileName);
        audio.play();
        fig = false;
      }
    }, false);
  } else {
    audio.src = "https://xiaou520.gitee.io/sound/".concat(fileName);
    audio.play();
  }
};

uToolsUtils.isNewVersion = function () {
  // 当前版本
  var pluginInfo = window.pluginInfo;
  console.log(pluginInfo); // utools 自动更新数据处理

  if (read('version') !== pluginInfo.version) {
    uToolsUtils.playPromptTone('update.wav'); // 是否需要更新数据

    if (window.pluginInfo.update) {
      console.log('更新数据');
      var oldOss = read('oss');

      if (oldOss) {
        var oldOsskeys = oldOss;
        var newOssKeys = store.state.oss;
        newOssKeys.aliOss = oldOsskeys.aliOss;
        newOssKeys.tencentOss = oldOsskeys.tencentOss;
        newOssKeys.rruu = oldOsskeys.rruu;

        if (oldOsskeys.GitHub) {
          newOssKeys.GitHub = oldOsskeys.GitHub;
        }

        if (oldOsskeys.smMs) {
          newOssKeys.smMs = oldOsskeys.smMs;
        }

        if (!oldOss.aliOss.uploadDirectory) {
          newOssKeys.aliOss.uploadDirectory = '';
        }

        uToolsUtils.save('oss');
      }
    }

    update('version', pluginInfo.version); // 打开更新信息窗口

    window.createUpdateWindow();
    return true;
  } else {
    console.log('false');
    return false;
  }
};

