import "core-js/modules/es.array.concat";
import "core-js/modules/es.function.name";
import "core-js/modules/es.object.to-string";
import "core-js/modules/es.regexp.exec";
import "core-js/modules/es.string.replace";
import "core-js/modules/es.string.split";
import store from '../store/index';

function GitHub() {}

GitHub.uploadImage = function (item, id) {
  console.log('g', item);
  return new Promise(function (resolve, reject) {
    var fileName = "".concat(Date.now(), "-").concat(item.name);
    var fr = new FileReader();
    fr.readAsDataURL(item);

    fr.onloadend = function (e) {
      var base64 = e.target.result.split(',')[1];
      console.log(base64);
      var data = {
        message: '图床',
        content: base64
      };
      fetch("https://api.github.com/repos/".concat(store.state.oss.GitHub.project, "/contents").concat(store.state.oss.GitHub.path, "/").concat(fileName), {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json; charset=utf-8',
          Authorization: "token ".concat(store.state.oss.GitHub.token)
        },
        body: JSON.stringify(data)
      }).then(function (res) {
        return res.json();
      }).then(function (res) {
        if (res.content && res.content.download_url) {
          // https://raw.githubusercontent.com/xiaou66/picture/master/1597318943475-logo.png
          // https://cdn.jsdelivr.net/gh/xiaou66/picture/master/1597318943475-logo.png
          if (item.size >= 20 * 1024 * 1024) {
            resolve({
              status: 200,
              url: res.content.download_url,
              id: id
            });
          } else {
            var baseUrl = res.content.download_url.replace('/master/', '/').replace('https://raw.githubusercontent.com', '');
            var jsdelivrUrl = "https://cdn.jsdelivr.net/gh".concat(baseUrl);
            resolve({
              status: 200,
              url: jsdelivrUrl,
              id: id
            });
          }
        } else {
          resolve({
            status: 403,
            message: '上传失败'
          });
        }
      }).catch(function () {
        resolve({
          status: 403,
          message: '上传失败'
        });
      });
    };
  });
};

export default GitHub;